.c-Button {
  font-family: 'futura-pt', 'Open Sans', 'Helvetica Neue', 'Helvetica', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  word-spacing: 1px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 0.75rem 1rem;
  padding: calc(0.5 * var(--lh)) 1rem;
  padding: 0.5lh 1rem;
  font-size: 0.875em;
  white-space: nowrap;
  vertical-align: middle;
  background-color: #0c5fa8;
  border-radius: 0.25rem;
  box-shadow: 0 0.125em 0 0 #cdcdcd;
  cursor: pointer;
}
.c-Button,
a.c-Button,
.c-Button:hover,
a.c-Button:hover,
.c-Button:focus,
a.c-Button:focus {
  color: #fff;
}
.c-Button:hover,
.c-Button:focus {
  background-color: #0e6cc0;
}
.c-Button:active {
  background-color: #0a5290;
  box-shadow: 0 0.125em 0 0 #cdcdcd inset;
}
button.c-Button:disabled,
.c-Button--disabled {
  opacity: 0.25;
  cursor: not-allowed;
  filter: grayscale(100%);
}
